body{
    background: rgb(100, 255, 206);    
}
.wrapper{
    display: flex;
    height: 100vh;
    width: 100vw;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
}

.wrapper button{
    padding: 10px 20px;
    background: rgb(31, 1, 83);
    color: white;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}

.accordian{
    width: 500px;
}

.item{
    background: rgb(31, 1, 83);
    margin-bottom: 10px;
    padding: 10px 20px;
}

.title{
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.content{
    color: white;
    height: auto;
}